import styled from 'styled-components';

export const CategoriesWrapper = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
`

export const CategoriesBtn = styled.button`
    margin: 20px 0;
    padding: 10px;
`




